.catalog {
  margin-bottom: 31px;
}

@media (min-width: $tablet-width) {
  .catalog {
    margin-bottom: 50px;
  }
}

@media (min-width: $desktop-width) {
  .catalog {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    margin-bottom: 57px;
  }
}

@media (min-width: $desktop-width) {
  .catalog {
    display: flex;

    width: 100%;
  }
}
