.button {
  display: block;

  width: 100%;
  height: 52px;

  margin: 0;

  color: $second-color;
  font-family: "OpenSans bold", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid $second-color;

  &:hover {
    color: #ffffff;
    background-color: $dark-grey;
  }

  &:active {
    color: rgba(256, 256, 256, 0.3);
  }
}

.button--feedback {
  padding-top: 12px;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;
}

.button--write-us {
  width: 260px;
  margin: 0;
}

@media (min-width: 768px) {
  .button {
    &--feedback {
      width: 175px;
      padding-top: 15px;
    }

    &--order {
      width: 290px;
    }

    &--write-us {
      width: 230px;
    }
  }
}
